import React, { Component } from "react";
import IntlMessages from "../../../../components/utility/intlMessages";
import { Form, Col, Row, Modal, DatePicker } from "antd";
import getLanguage from "../../../App/MyLanguange";
import moment from "moment";

const range = (start, end) => {
  start = parseInt(start);
  end = parseInt(end);
  const result = [];
  for (let i = start; i <= end; i++) {
    result.push(i);
  }
  return result;
}

class ModalConfirmReceive extends Component {
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        values.received_at = values.received_at.format(
          "YYYY-MM-DD HH:mm:ss"
        );
        this.props.submit(values);
      }
    });
  };

  validateReceiveDate = (rule, value, callback) => {
    const { transaction_date } = this.props;

    if (transaction_date.isAfter(value)) {
      callback(<IntlMessages id="receiveDateMoreThanTransDate" />);
    }

    if (value.isAfter(moment())) {
      callback(<IntlMessages id="receiveDateLessThanCurrentDate" />);
    }

    callback();
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator } = this.props.form;
    const { loading, transaction_date } = this.props;

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    return (
      <Modal
        title={<IntlMessages id="button.receiveTransaction" />}
        visible={this.props.visible}
        centered
        onCancel={this.props.handleModal}
        onOk={this.handleSubmit}
        width="35%"
        okText={getLanguage({ id: "button.ok" })}
        cancelText={getLanguage({ id: "button.cancel" })}
        okButtonProps={{ loading: loading }}
        cancelButtonProps={{ loading: loading }}
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <Row type="flex" justify="start" gutter={6}>
            <Col span={24}>
              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="itemTransfers.receiveDate" />}
              >
                {getFieldDecorator("received_at", {
                    initialValue: moment(),
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }, {
                        validator: this.validateReceiveDate
                      }
                    ]
                  })(
                    <DatePicker
                      style={{ width: "100%" }}
                      showTime={{
                        format: "HH:mm:ss",
                        defaultValue: transaction_date
                          ? moment(transaction_date.format("HH:mm:ss"), "HH:mm:ss")
                            .isBefore(moment(moment().format("HH:mm:ss"), "HH:mm:ss")) 
                              ? moment(transaction_date.format("HH:mm:ss"), "HH:mm:ss") 
                              : moment(moment().format("HH:mm:ss"), "HH:mm:ss") 
                          : moment(moment().format("HH:mm:ss"), "HH:mm:ss") 
                      }}
                      format="DD MMMM YYYY HH:mm:ss"
                      allowClear={false}
                      disabledDate={e => 
                        e < moment(transaction_date).startOf("day") || e > moment().endOf("day")
                      }
                      disabledTime={(e) => {
                        if (e && e.format("DD MM YY HH:mm:ss") === moment().format("DD MM YY HH:mm:ss")) {
                          return {
                            disabledHours: () => range(parseInt(moment().format("HH")) + 1, moment().endOf("day").format("HH")),
                            disabledMinutes: () => range(parseInt(moment().format("mm")) + 1, moment().endOf("day").format("mm")),
                            disabledSeconds: () => range(parseInt(moment().format("ss")) + 1, moment().endOf("day").format("ss"))
                          }
                        } else if (e && e.format("DD MM YY HH:mm") === moment().format("DD MM YY HH:mm")) {
                          return {
                            disabledHours: () => range(parseInt(moment().format("HH")) + 1, moment().endOf("day").format("HH")),
                            disabledMinutes: () => range(parseInt(moment().format("mm")) + 1, moment().endOf("day").format("mm")),
                            disabledSeconds: () => range(moment().format("ss"), moment().endOf("day").format("ss"))
                          }
                        } else if (e && e.format("DD MM YY HH") === moment().format("DD MM YY HH")) {
                          return {
                            disabledHours: () => range(parseInt(moment().format("HH")) + 1, moment().endOf("day").format("HH")),
                            disabledMinutes: () => range(parseInt(moment().format("mm")) + 1, moment().endOf("day").format("mm")),
                            disabledSeconds: () => []
                          }
                        } else if (e && e.format("DD MM YY") === moment().format("DD MM YY")) {
                          return {
                            disabledHours: () => range(parseInt(moment().format("HH")) + 1, moment().endOf("day").format("HH")),
                            disabledMinutes: () => [],
                            disabledSeconds: () => []
                          }
                        } 
                        
                        if (e && e.format("DD MM YY HH:mm:ss") === transaction_date.format("DD MM YY HH:mm:ss")) {
                          return {
                            disabledHours: () => range(0, parseInt(moment(transaction_date).format("HH")) - 1),
                            disabledMinutes: () => range(0, parseInt(moment(transaction_date).format("mm")) - 1),
                            disabledSeconds: () => range(0, parseInt(moment(transaction_date).format("ss")) - 1),
                          }
                        } else if (e && e.format("DD MM YY HH:mm") === transaction_date.format("DD MM YY HH:mm")) {
                          return {
                            disabledHours: () => range(0, parseInt(moment(transaction_date).format("HH")) - 1),
                            disabledMinutes: () => range(0, parseInt(moment(transaction_date).format("mm")) - 1),
                            disabledSeconds: () => range(0, parseInt(moment(transaction_date).format("ss")) - 1),
                          }
                        } else if (e && e.format("DD MM YY HH") === transaction_date.format("DD MM YY HH")) {
                          return {
                            disabledHours: () => range(0, parseInt(moment(transaction_date).format("HH")) - 1),
                            disabledMinutes: () => range(0, parseInt(moment(transaction_date).format("mm")) - 1),
                            disabledSeconds: () => [],
                          }
                        } else if (e && e.format("DD MM YY") === transaction_date.format("DD MM YY")) {
                          return {
                            disabledHours: () => range(0, parseInt(moment(transaction_date).format("HH")) - 1),
                            disabledMinutes: () => [],
                            disabledSeconds: () => [],
                          }
                        }               
                      }}
                      // placeholder={getLanguage({id: "itemTransfers.receiveDate.placeholder"})}
                    />
                  )}
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </Modal>
    );
  }
}

const WrappedConfirmReceiveModal = Form.create({ name: "confirm_receive_modal" })(
  ModalConfirmReceive
);

export default WrappedConfirmReceiveModal;
